@import '../../../scss/vars';

.cryptoStats {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  margin-bottom: 70px;

  &__statTitle {
    color: $light-grey;
    margin-bottom: 10px;
    font-size: 16px;
  }

  &__statValue {
    font-size: 22px;
  }
}

@media (max-width: 431px) {
  .cryptoStats {
    margin-bottom: 35px;
  }
}
